<template>
  <Layout class="problem-page">
    <Row class="tool-bar" type="flex">
      <div class="left">
        <Input class="search-input" v-model="searchKey" placeholder="请输入关键字搜索" @keyup.native.enter="getProblemList">
          <Button slot="append" icon="md-search" @click.native="getProblemList"/>
        </Input>
      </div>
      <div class="right">
        <Button v-if="selectedProblem.length" style="mergin-right: 0.5rem" type="error" @click.native="deleteProblem">删除</Button>
        <Button type="primary" @click.native="addNewProblem">新增</Button>
      </div>
    </Row>
    <Row class="tags">
      <Tag color="success" @click.native="searchTag">全部</Tag>
      <Tag color="success" v-for="(item, index) in tagList" :key="index" @click.native="searchTag(item)">{{item.tagName}}</Tag>
    </Row>
    <Row class="problem-list">
      <Table class="problem-table" :columns="columns" :data="problemList" @on-selection-change="selectProblem"></Table>
      <!-- <Page :total="40" show-elevator show-sizer></Page> -->
    </Row>
    <Row class="pagination">
      <Page :total="paginationData.total" :current="paginationData.page" show-elevator show-total show-sizer @on-change="changePage" @on-page-size-change="changePageSize"></Page>
    </Row>
  </Layout>
</template>

<script>
import { getProblemList, deleteProblem } from '@/api/problem'
import { getTagesByRelation } from '@/api/knowledge'

export default {
  name: 'problem',
  data () {
    return {
      searchKey: '',
      currentEditData: {},
      columns: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          width: 60,
          render: (h, { row, column, index }) => {
            return h(
              'span',
              `#${index + 1}`
            )
          }
        },
        {
          title: '标题',
          key: 'title',
          render: (h, { row, column, index }) => {
            return h(
              'a',
              {
                attrs: {
                  href: 'javascript: void(0)'
                },
                on: {
                  click: () => {
                    this.openEditModal(row)
                  }
                }
              },
              row.title
            )
          }
        },
        {
          title: '标签',
          key: 'tag',
          render (h, data) {
            const tags = []
            data.row.tagInfo = data.row.tagInfo || []
            for (let tag of data.row.tagInfo) {
              tags.push(h(
                'Tag',
                {
                  props: {
                    color: 'success'
                  }
                },
                tag.name
              ))
            }
            return h(
              'div',
              {
                attrs: {
                  class: 'problem-tags'
                }
              },
              tags
            )
          }
        },
        {
          title: '内容',
          render: (h, { column, row, index }) => {
            return h(
              'span',
              {
                style: {
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap'
                }
              },
              row.content
            )
          }
        }
      ],
      problemList: [],
      paginationData: {
        page: 1,
        pageSize: 10,
        total: 0
      },
      selectedProblem: [],
      tagList: [],
      selectedTag: null
    }
  },
  computed: {
    showEditModal: {
      get () {
        return JSON.stringify(this.currentEditData) !== '{}'
      },
      set (value) {
        if (!value) {
          this.currentEditData = {}
        }
      }
    }
  },
  created () {
    this.getProblemList()
    this.getTagList()
  },
  mounted () {},
  methods: {
    getTagList () {
      getTagesByRelation({
        type: 6
      }).then(({ data, response }) => {
        if (response.success) {
          this.tagList = data
        } else {
          this.$Message.error(response.msg)
        }
      })
    },
    getProblemList () {
      getProblemList({
        pageNo: this.paginationData.page,
        pageSize: this.paginationData.pageSize,
        title: this.searchKey,
        tagId: this.selectedTag ? this.selectedTag.tagId : null,
        type: 1
      }).then(resp => {
        if (resp.response.success) {
          this.problemList = resp.data.result
          this.paginationData.total = resp.data.totalCount
        } else {
          this.$Message.error(resp.response.msg)
        }
      }).catch(err => {
        console.error(err)
      })
    },
    addNewProblem () {
      this.$router.push({ name: 'ProblemDetail', params: { type: 'new', id: 0 } })
    },
    openEditModal (data) {
      this.$router.push({ name: 'ProblemDetail', params: { type: 'detail', id: data.id } })
    },
    changePage (page) {
      this.paginationData.page = page
      this.getProblemList()
    },
    changePageSize (size) {
      this.paginationData.pageSize = size
      this.getProblemList()
    },
    deleteProblem () {
      this.$Modal.confirm({
        title: '提示',
        content: '警告⚠️删除后数据不可恢复，请确定是否删除？',
        onCancel: () => {},
        onOk: () => {
          deleteProblem({
            ids: this.selectedProblem.map(item => item.id)
          }).then(({ data, response }) => {
            if (response.success) {
              this.$Message.success('删除成功！')
              this.getProblemList()
            } else {
              this.$Message.error(response.msg)
            }
          })
        }
      })
    },
    selectProblem (selection) {
      this.selectedProblem = selection
    },
    searchTag (tag) {
      this.selectedTag = tag
      this.getProblemList()
    }
  }
}
</script>

