  background none
  height 100%
  .tool-bar
    flex 1 0 auto
    padding-bottom 0.5rem
    display flex
    justify-content space-between
    .left
      flex 1 1 auto
      .search-input
        max-width 20rem
    .right
      flex 1 0 auto
      text-align right
  .tags
    flex 1 0 auto
    padding-bottom 0.5rem
  .problem-list
    flex 1 1 auto
    height 100%
    margin-bottom 0.5rem
    overflow hidden
    .problem-table
      height 100%
      flex 1 1 auto
      overflow hidden
</style>

